import Vue from 'vue'
import Router from 'vue-router'

const Lottery = r => require.ensure([], () => r(require('@/components/gerenchoujiang/Lottery.vue')))
const LotteryGift = r => require.ensure([], () => r(require('@/components/gerenchoujiang/LotteryGift.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Lottery',
      component: Lottery
    },
    {
      path: '/lotteryGift',
      name: 'lotteryGift',
      component: LotteryGift
    },
  ]
})



